*{
  margin:0;
  padding:0;

}
.container{
  display:flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  width:400px;
  height:300px;
  border-radius: 15px;
  background-image:linear-gradient(rgb(200, 200, 242),white) ;
  margin-bottom: 20px;
}
.username{
  size:15px;
  color: brown;
  font-size: 15px;
  margin:5px;
}
.name{
  size:15px;
  color: brown;
  font-size: 15px;
  margin:5px;

}
.card{
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  column-gap: 50px;
}